		console.log(accesstoken);
		if (this.micId != "" && this.micId != undefined) {
			if (accesstoken == "" || accesstoken == undefined) {
				that.isFrist = true
			} else {
				that.isFrist = false;
				if (!this.isTurn) {
					//二维码-扫码后（小程序通过棒棒糖的二维码的值去获取对应的音频详情）
					this.audioQcode();
				}

			}
		}

		this.micTabStatus = 0;
		this.micList = uni.getStorageSync("micList")
		this.micTabList = uni.getStorageSync("micList")
		var obj = {
			all: "修改了",
			info: "我被修改了",
			name: "我被修改了"
		}
		this.SET_MICINFO(obj)

	},

	onPageScroll: function(e) {

		if (e.scrollTop > this.headerTop) {
			this.isTop = true
		} else {
			this.isTop = false
		}
	},
	methods: {
		...mapMutations([
			"SET_MICLIST",
			"SET_MICINFO",
			"SET_MICSTATUS"
		]),
		appTest() {







		},
		// 去注册
		homeGoRegister() {
			uni.navigateTo({
				url: "/pages/login/login"
			})
		},
		// 商品详情
		go_shopDetails(e) {
			console.log(e)
			uni.navigateTo({
				url: "/pages/shopDetails/shopDetails?id=" + e
			})
		},
		// 去明星音频
		goStar() {
			uni.navigateTo({
				url: "/pages/starMusic/starMusic"
			})
		},
		// 去糖盒
		goTbox() {
			uni.navigateTo({
				url: "/pages/myTbox/myTbox"
			})
		},
		// 排行
		goRanking() {
			uni.navigateTo({
				url: "/pages/ranking/ranking"
			})
		},
		// 音乐tab
		micTabClick(idx, id) {
			var that = this;
			that.micTabStatus = idx;
			that.scrollLeft = (idx - 2) * 90;

			let micList = that.micList;
			let micTabList = [];

			micList.map((currentValue, index, arr) => {
				if (currentValue.version == id) {
					micTabList.push(currentValue)
				}
			})
			that.micTabList = micTabList
			if (id == 0) {
				that.micTabList = micList
			}

		},
		// 关闭启动页
		close() {
			this.initStatus = true
		},
		// 启动页轮播
		swiper_change(e) {
			let that = this;
			console.log(e.detail)
			if (e.detail.current == 2) {
				setTimeout(function() {
					that.close()
				}, 1000);
			}
		},
		goMic() {
			uni.navigateTo({
				url: "/pages/micPlayPage/micPlayPage"
			})
		},
		/**播放视屏 */
		play(e) {
			//执行全屏方法  
			var videoContext = wx.createVideoContext('myvideo', this);
			videoContext.requestFullScreen();

			this.fullScreen = true;

		},
		/**关闭视屏 */
		closeVideo() {
			//执行退出全屏方法
			var videoContext = wx.createVideoContext('myvideo', this);
			videoContext.exitFullScreen();
			this.fullScreen = false;
		},
		/* // /**视屏进入、退出全屏 */
		// fullScreen(e) {
		// 	var isFull = e.detail.fullScreen;
		// 	//视屏全屏时显示加载video，非全屏时，不显示加载video
		// 	this.fullScreen = isFull;
		// }, */
		getScancode: function() {
			var _this = this;
			// 允许从相机和相册扫码
			uni.scanCode({
				success: function(res) {
					uni.showToast({
						title: "条码类型:" + res.scanType + "，条码内容：" + res.result,
						duration: 2000,
						icon: "none"
					})
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
				}
			});
		},
		//二维码-扫码后（小程序通过棒棒糖的二维码的值去获取对应的音频详情）
		async audioQcode(e) {
			const that = this;
			let accesstoken = that.accesstoken;
			try {
				let data = await that.$wxhttp.post({
					url: '/lollipop/api/v1/get_audio_from_qrcode',
					data: {
						id: accesstoken.id,
						access_token: accesstoken.access_token,
						qr_code: this.micId,
					},
				})
				console.log(data.data);
				uni.showToast({
					title: data.data.audio_name,
					duration: 2000,
					icon: "none"
				})
				//存入我的糖盒
				that.myTboxFun(data.data.audio_id);

			} catch (error) {
				console.log(error);
			}

		},
		myTboxFun: function(audio_id) {
			//获取歌单
			var micList = this.micList;
			var tboxList = uni.getStorageSync("tbox_audio") || [];
			if (tboxList != "" && tboxList != undefined) {
				for (var j = 0; j < tboxList.length; j++) {
					if (tboxList[j].audio_id != audio_id) {
						tboxList.push(micList.find(item => item.audio_id = audio_id))
					}
				}
			} else {
				tboxList.push(micList.find(item => item.audio_id = audio_id))
			}



			console.log("歌单获取" + tboxList);
			uni.setStorageSync("tbox_audio", tboxList);
			//跳转到音乐播放器
			this.goAudio(audio_id);
		},
		// 音乐
		goAudio(audio_id) {
			this.isTurn = true; //设置为已经跳转
			uni.navigateTo({
				url: "/pages/micPlayPage/micPlayPage?id=" + audio_id
			})
		},
	}
}
